import usePositionStore from '@/store/position'
import { Token } from '@cetus/types'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble, textEllipses } from '@cetus/utils'
import { HStack, Skeleton, Text } from '@chakra-ui/react'
import { useState } from 'react'

function PositionCurrentPrice({
  posId,
  displayTokenA,
  displayTokenB,
  symbolEllipsesDecimals = 10 //symbolË∂ÖÂá∫‰ΩçÊï∞ÂêéÊòØÂê¶Â±ïÁ§∫...
}: {
  posId: string
  displayTokenA: Token
  displayTokenB: Token
  symbolEllipsesDecimals?: number
}) {
  const [isDirect, setIsDirect] = useState(true)
  const { posPoolsRelatedData } = usePositionStore()
  const currentPosPoolsRelatedData = posPoolsRelatedData[posId]

  return (
    <HStack>
      <Skeleton isLoaded={!!currentPosPoolsRelatedData?.currentPrice}>
        <Text color="text_caption">{isDirect ? currentPosPoolsRelatedData?.currentPrice : currentPosPoolsRelatedData?.currentPriceReverse}</Text>
      </Skeleton>
      <HStack gap="0">
        <Text color="primary_gray">{textEllipses(isDirect ? displayTokenB?.symbol : displayTokenA?.symbol, symbolEllipsesDecimals)}</Text>
        <Text color="primary_gray">&nbsp;per&nbsp;</Text>
        <Text color="primary_gray">{textEllipses(isDirect ? displayTokenA?.symbol : displayTokenB?.symbol, symbolEllipsesDecimals)}</Text>
      </HStack>
      <Icon
        xlinkHref="#icon-icon_swap1"
        w="16px"
        h="16px"
        onClick={(e: any) => {
          cancelBubble(e)
          setIsDirect(!isDirect)
        }}
      />
    </HStack>
  )
}

export default PositionCurrentPrice
