import useBurn from '@/hooks/burn/useBurn'
import { MsafeTransactionSubType, PosBaseInfo, PosReward } from '@/types'
import { deleteOx } from '@/utils/contract-helper'
import useClmmSDKStore from '@cetus/stores/src/useClmmSDKStore'
import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'

export default function usePosClaimFeeAndReward() {
  const { clmmSdk } = useClmmSDKStore()
  const { peripherySdk, burnSdk } = usePeripherySDKStore()
  const { getBurnClaimTxPayload } = useBurn()

  /**
   * Êî∂Ââ≤‰ªì‰ΩçÁöÑfeeÂíårewardÔºåÊîØÊåÅclmm, farms, burn
   * @param params
   * @returns
   */
  const getPosClaimFeeAndRewardTxPayload = async (params: {
    posType: 'clmm' | 'farms' | 'burn'
    id: string
    clmmPool: string
    coinTypeA: string
    coinTypeB: string
    rewarderCoinTypes?: any
    account?: string
  }) => {
    const { posType, id, clmmPool, coinTypeA, coinTypeB, rewarderCoinTypes, account } = params
    let tx, msafeParams
    if (posType === 'clmm') {
      const parameter = {
        pool_id: clmmPool,
        pos_id: id,
        rewarder_coin_types: rewarderCoinTypes,
        coinTypeA,
        coinTypeB,
        collect_fee: true
      }

      tx = await clmmSdk.Rewarder.collectRewarderTransactionPayload(parameter)

      msafeParams = {
        action: MsafeTransactionSubType.ClaimFeeAndMining,
        txbParams: parameter
      }
    } else if (posType === 'farms') {
      const parameter = {
        coinTypeA,
        coinTypeB,
        clmm_pool_id: clmmPool,
        position_nft_id: id,
        clmm_rewarder_types: rewarderCoinTypes,
        collect_fee: true
      }

      tx = await peripherySdk.Farms.claimFeeAndClmmReward(parameter)

      msafeParams = {
        action: 'FarmingClaimFeeAndReward',
        txbParams: parameter
      }
    } else if (posType === 'burn') {
      const parameter = {
        poolAddress: clmmPool,
        posId: id,
        coinTypeA,
        coinTypeB,
        account: account as string,
        rewarderCoinTypes
      }

      tx = getBurnClaimTxPayload(parameter)
    }

    return {
      tx,
      msafeParams
    }
  }

  /**
   * ÊâπÈáèÊî∂Ââ≤FeeÂíåRewards
   * @param posBaseList
   * @param posRewardsData
   * @param account
   * @returns
   */
  const getBatchHarvestFeeAndRewardsTxPayload = async (posBaseList: PosBaseInfo[], posRewardsData: Record<string, PosReward[]>, account: string) => {
    console.log('üöÄ ~ getBatchHarvestFeeAndRewardsTxPayload ~ posBaseList:', posBaseList, posRewardsData, account)
    const clmmHarvestParams: any = []
    const framsHavestParams: any = []
    const burnHavestParams: any = []
    const farmsParams: any = []

    posBaseList.forEach(position => {
      const rewarderCoinTypes = posRewardsData[position.posId]?.map((item: PosReward) => item.coin_address) || []
      console.log('üöÄ ~ getBatchHarvestFeeAndRewardsTxPayload ~ rewarderCoinTypes:', rewarderCoinTypes)

      if (position.posType == 'clmm') {
        clmmHarvestParams.push({
          pool_id: position.clmmPool,
          pos_id: position.id,
          collect_fee: true,
          rewarder_coin_types: rewarderCoinTypes,
          coinTypeA: deleteOx(position.coinTypeA),
          coinTypeB: deleteOx(position.coinTypeB)
        })
      } else if (position.posType == 'farms') {
        framsHavestParams.push({
          pool_id: position.farmsPool,
          position_nft_id: position.id,
          clmm_pool_id: position.clmmPool,
          collect_fee: true,
          collect_frams_rewarder: false,
          clmm_rewarder_types: rewarderCoinTypes,
          coinTypeA: position.coinTypeA,
          coinTypeB: position.coinTypeB
        })
        farmsParams.push({
          pool_id: position.farmsPool,
          position_nft_id: position.id
        })
      } else {
        burnHavestParams.push({
          pos: position?.id,
          pool: position.clmmPool,
          coinTypeA: position.coinTypeA,
          coinTypeB: position.coinTypeB,
          rewarderCoinTypes,
          account
        })
      }
    })

    const batchHarvestAndClmmFeeTx = await peripherySdk.Farms.batchHarvestAndClmmFeePayload(framsHavestParams, clmmHarvestParams)

    const burnRewardList: any = []
    for (let i = 0; i < burnHavestParams.length; i++) {
      const item = burnHavestParams[i]
      if (item?.rewarderCoinTypes.length > 0) {
        burnRewardList.push(item)
      }
    }
    const txb = burnSdk.Burn.createCollectFeesPaylaod(burnHavestParams, batchHarvestAndClmmFeeTx)

    const tx = burnSdk.Burn.crateCollectRewardsPayload(burnRewardList, txb)

    const txf = await peripherySdk.Farms.batchHarvestPayload(farmsParams, tx)

    return { txb, tx, txf }
  }

  return {
    getPosClaimFeeAndRewardTxPayload,
    getBatchHarvestFeeAndRewardsTxPayload
  }
}
